@font-face {
  font-family: "Press Start 2P";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Press Start 2P"), local("PressStart-2P"), url("../fonts/PressStart2P.woff2") format("woff2"), url("../fonts/PressStart2P.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  max-width: 100vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Press Start 2P", "Arial", sans-serif;
  color: #ffffff;
  text-shadow: 2px 1px 1px #000000;
  background: linear-gradient(#8A2BE2, #4B0082);
  overflow-x: hidden; }

.visually-hidden {
  width: 1px;
  height: 1px;
  position: absolute;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

