*** 2. Основные стили ***
********************** */
.pixelart__header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 60px; }

.pixelart__main-title {
  margin: 0;
  padding: 0; }

.drawing-board__toolkit {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  outline: 2px dashed red; }
